import React, { useState, useEffect } from "react";
import Rectangle88 from "../../assets/images/Rectangle 88.png";
import Ellipse24 from "../../assets/images/Ellipse 24.png";
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { Avatar, Badge, Input, Space, Radio, Button } from 'antd';
import { FaRegCalendarCheck } from "react-icons/fa";
import Sidebar from "../../components/sidebar";
import VFM_Avatar from "../../components/common/avatar";
import CustomModal from "../../components/custommodal"
import RegFormModal from "../../components/common/modal/RegForm";
import SuccessModal from "../../components/common/modal/success";
import ImageUploader from "../../components/common/ImageUploader";
import imageProduct from "../../assets/images/image 35.png";
import { TagGroup } from "../../components/common/tagGroup";
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';

import { transactionsData, purchasedCasesData, chatProgressData, requestData } from '../../constant/data'

const DesignerProfile = () => {
    const [active, setActive] = useState('REQUEST')
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();



    useEffect(() => {
        console.log(active)
    }, [active])

    const handleActive = (current) => {
        // alert(current)
        setActive(current)
    }

    const [open, setOpen] = useState(false);
    const [popupdata, setPopupdata] = useState("");
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const [openConfirm, setOpenConfirm] = useState(false);
    const handleConfirm = () => setOpenConfirm(true);
    const handleConfirmClose = () => setOpenConfirm(false);

    const openPopUp = (data) => {
        setPopupdata(data);
        handleClose();
        handleConfirm();
    }
    const [openForm, setOpenForm] = useState(false);
    const [open1, setOpen1] = useState(false);
    const [chatRegister, setChatRegister] = useState(false);
    const hadnleOpenForm = () => setOpenForm(true);
    const hadnleCloseForm = () => setOpenForm(false);



    const handleOpen1 = () => setOpen1(true);
    const handleClose1 = () => setOpen1(false);

    const closefun = () => {
        hadnleCloseForm();
        handleOpen1();
    };


    return (
        <>
            {/* <Header page={"webscreens"} pagetitle={"Creator"} /> */}
            <section id="banner-section">
                <ImageUploader img={Rectangle88} alt='wait' isBack={true} />
                <div className="relative">
                    <div className="relative flex top-[-60px] md:justify-start md:left-20 justify-center">
                        <ImageUploader img={Ellipse24} alt='wait' isBack={false} />
                    </div>
                    <div className="md:relative md:top-[-120px] left-56">
                        <h1 className="m-0">@DESIGNER_USER000</h1>
                        <p className="m-0">依頼専門。好きなジャンル➡サイバーパンク・和・等々</p>
                    </div>
                    <Button type="primary"
                        onClick={hadnleOpenForm}
                        className="flex md:top-[-160px] md:left-[50%] md:relative mt-3 text-white px-3 w-28 rounded-lg text-center border-none justify-center"
                    >
                        {t("To Edit")}
                    </Button>
                    <Button type="primary" className="flex md:relative md:top-[-120px] top-[-30px] left-32 text-white p-2 px-10 rounded-lg text-center">{t("ARTIST")}</Button>
                </div>

            </section>

            <section className="bg-gray-100 md:p-16 pl-16 pr-2 gap-2 justify-center py-20 grid grid-cols-12">
                <div className="md:col-span-3 col-span-1">
                    <Sidebar setCurrent={handleActive} isProfile={true} />
                </div>
                <div className="md:col-span-9 col-span-11  shadow-md shadow-gray-200 bg-white overflow-scroll p-5 max-h-screen">
                    <div >
                        <h1 className="border-b-7 border-blue-600 text-xl my-8 ml-3">{t("@DESIGNER_USERR00")}</h1>
                        <div className="flex gap-5 flex-wrap justify-center">
                            {requestData.map((request) => (
                                <div key={request.id} onClick={() => { navigate(request.navigateTo) }} className="w-60 shadow-lg shadow-gray-500 relative">
                                    <img src={request.img} className="w-full"></img>
                                    <div className="px-4 py-[2px] text-xs bg-badge w-auto rounded-lg text-white text-center absolute top-2 left-2">
                                        <p className="font-semibold">Gallery</p>
                                        <span>{request.title}</span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div >
            </section >
            <CustomModal open={open} handleClose={handleClose}>
                <div>
                    <p className="text-badge font-bold">{t("Edit Username")}</p>
                    <input
                        className="p-2 w-[90%] border border-gray-400"
                        type="text"
                        placeholder="例）USER_00000"
                    />
                    <p className="text-badge font-bold">{t("Edit Your Introduction")}</p>
                    <textarea
                        className="p-2 w-[90%] border border-gray-400 h-[150px]"
                        type="text"
                        placeholder="例）はじめまして。好きなジャンルは〇〇です。"
                    />
                    <button
                        onClick={() => { openPopUp("Registration of profile has been completed") }}
                        className="register-popup-value"
                    >
                        {t("Register")}
                    </button>
                </div>
            </CustomModal>
            <CustomModal open={open} handleClose={handleClose}>
                <div>
                    <h1 className="text-lg text-badge font-bold">ユーザーネームを編集する</h1>
                    <input className="p-4 w-full border border-solid border-gray-800" type="text" placeholder="例）USER_00000" />
                    <h1 className="text-lg text-badge font-bold mt-3">自己紹介を編集する</h1>
                    <textarea className="w-full h-40 border border-solid border-gray-800 p-4" placeholder="例）はじめまして。好きなジャンルは〇〇です。" />
                    <p>あと100文字（全角）</p>
                    <button onClick={() => { handleClose() }} className="w-1/3 p-2.5 border-none bg-gray-800 text-white rounded-md block mx-auto mt-6 hover:opacity-50 duration-500 shadow-md shadow-gray-500">登録する</button>
                </div>
            </CustomModal>
            <CustomModal open={openConfirm} handleClose={handleConfirmClose}>
                <div>
                    <p className="text-center">{t(popupdata)}</p>
                </div>
            </CustomModal>
            <RegFormModal open={openForm} onCancel={hadnleOpenForm} onConfirm={closefun} />
            <SuccessModal open={open1}
                onCancel={handleClose1}
                title="定型文の登録が完了しました。"
            />
        </>
    );
}

export default DesignerProfile;


